export default {
  props: {
    // ID
    id: String,
    // 
    visible: {
      type: Boolean,
      default: true
    },
    // 
    title: String,
    // 
    index: Number
  },
  data () {
    return {
      // layer
      layer: null,
      type: 'layer',
      // 
      defaultIndex: 0,
      // 
      map: null,
      // 
      viewContainer: null
    }
  },
  watch: {
    /**
     * 
     * @param _new
     * @param old
     */
    visible: function (_new, old) {
      if (_new !== old) {
        if (this.layer !== null) {
          this.layer.visible = _new
        } else if (_new === true) {
          this.init()
        }
      }
    }
  },
  methods: {
    /**
     * 
     * @param index
     */
    setDefaultIndex (index) {
      this.defaultIndex = index
    },
    /**
     * 
     */
    getVisible () {
      return this.visible
    },
    /**
     * 
     */
    getIndex: function () {
      if (this.index !== null) {
        return this.index
      } else {
        return this.defaultIndex
      }
    }
  }
}
